.label-status {
  background-color: $bg-button;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: pxToRem(445);
  height: pxToRem(139);

  &__icon {
    width: pxToRem(127);
    height: pxToRem(127);
    position: absolute;
    left: 0;
    bottom: 0;
    object-fit: contain;
    opacity: 0.35;
  }

  &__body {
    display: flex;
  }

  &__content {
    margin: 0 pxToRem(10);
  }

  &__title {
    font-size: pxToRem(15);
    line-height: pxToRem(22);
    letter-spacing: pxToRem(-0.45);
    margin-bottom: pxToRem(10);
    color: $white;
  }

  &__group-value {
    // color: $color-label-power;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }

  &__value {
    font-family: $Roboto-Font;
    font-weight: bold;
    font-size: pxToRem(40);
    line-height: pxToRem(48);
    letter-spacing: pxToRem(-1.2);
    margin-right: pxToRem(7);
  }

  &__unit {
    font-size: pxToRem(14);
    line-height: pxToRem(20);
    letter-spacing: pxToRem(-0.42);
  }

  &__group-unit {
    position: absolute;
    bottom: pxToRem(6);
    right: pxToRem(10);
    // color: $color-label-power;
    font-size: pxToRem(14);
    line-height: pxToRem(20);
    letter-spacing: pxToRem(-0.42);
  }
}

.label-status-v2 {
  min-width: pxToRem(330);
  height: pxToRem(140);
  margin-right: pxToRem(10);
  padding: pxToRem(20);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  &.style3 {
    .wrap-icon {
      width: pxToRem(140);
      height: pxToRem(140);
      padding: pxToRem(30);
      position: relative;
      left: -1px;
    }

    .label-status__body {
      width: calc(100% - #{pxToRem(140)});
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  .label-status__body {

    .status__icon {
      width: pxToRem(85);
      margin-right: pxToRem(20);
    }

    .label-status__content {
      text-align: center;

      .label-status__title {
        margin-bottom: pxToRem(5);
      }
    }

    .label-status__value {
      font-size: pxToRem(40);
      font-weight: bold;
      line-height: 1.33;
      letter-spacing: pxToRem(-1.3);
      display: flex;
      align-items: center;

      i {
        font-style: normal;
        font-size: pxToRem(36);
      }

      span {
        font-size: pxToRem(14);
        font-weight: normal;
        line-height: 1.43;
        letter-spacing: pxToRem(-0.42);
        margin-left: pxToRem(3);
      }
    }

    * {
      color: $white;
    }
  }
}