/* noto-sans-kr-100 - korean */
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 100;
  src: url('../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.eot');
  /* IE9 Compat Modes */
  src: local('Noto Sans KR Thin'), local('NotoSansKR-Thin'),
    url('../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.eot?#iefix') format('embedded-opentype'),
    url('../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.woff2') format('woff2'),
    url('../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.woff') format('woff'),
    // url("../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.ttf") format("truetype"),
    url('../assets/fonts/NotoSans/noto-sans-kr-v8-korean-100.svg#NotoSansKR') format('svg');
  /* Legacy iOS */
}

